@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
*{margin:0;padding:0;font-family:'Poppins';font-weight:300}.container{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;background:url(/intro-component-with-signup-form/static/media/bg-intro-desktop.059183ee.png),#ff7a7a}.content{width:960px;display:flex;align-items:center;padding-left:100px;padding-right:100px}.col{width:50%}h2{width:270px;font-weight:bold;color:#FFF;font-size:2em}p{padding-right:30px;font-weight:300;color:#FFF;font-size:16px}.form{border-radius:8px;background-color:#FFF;padding:30px;box-shadow:0px 5px 4px 0px rgba(62,60,73,0.5)}.head{background-color:#6055a5;padding:10px 15px;text-align:center;border-radius:8px;margin-bottom:15px;box-shadow:0px 5px 4px 0px rgba(62,60,73,0.5);color:#FFF;font-weight:bold}.head>span{color:#b9b6d3;font-weight:300}form{width:100%}input{width:100%;height:40px;border-radius:5px;border:1px solid rgba(62,60,73,0.5);font-weight:bold}input[type="text"],input[type="password"]{box-sizing:border-box;padding-left:15px}input[type="text"]::-webkit-input-placeholder, input[type="password"]::-webkit-input-placeholder{outline:#ff7a7a}input[type="text"]:-ms-input-placeholder, input[type="password"]:-ms-input-placeholder{outline:#ff7a7a}input[type="text"]::placeholder,input[type="password"]::placeholder{outline:#ff7a7a}input.error{width:100%;height:40px;border-radius:5px;border:2px solid #ff7a7a}.input-text{display:flex;flex-direction:column;margin-bottom:15px;align-items:flex-end}.btn{color:#FFF;cursor:pointer;background-color:#38cc8c;padding:10px 15px;text-align:center;border-radius:8px;margin-bottom:15px;border-color:#38cc8c;text-transform:uppercase}.footer{text-align:center}.footer>span{font-weight:bold;color:#b9b6d3;font-size:10px}.footer>span>a{font-weight:bold;text-decoration:none;margin-left:3px;color:#ff7a7a}.error{margin-top:3px;font-size:10px;color:#ff7a7a}i.error-icon{content:url(/intro-component-with-signup-form/static/media/icon-error.b72f792e.svg);position:absolute;padding:10px;min-width:10px}@media (min-width: 220px) and (max-width: 750px){.container{width:auto;height:auto;padding-top:50px;padding-bottom:50px}.content{width:100%;flex-direction:column;padding:0px}.col{width:85%}h2,p{text-align:center;width:100%;padding:0;margin-bottom:30px}input{height:60px}}

